.navContainer {
    position: absolute;
    bottom: 0px;
    height: 71px;
    width: 100%;
}

.btnsWarper {
    height: 100%;
    width: 100%;
    /* position: relative; */
    background-color: #292E4E;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    /* color: #8E8E93; */
}

.btnDiv.active .btnSpan {
    color: white;
}

.btnDiv {}

.btnImg {
    cursor: pointer;
}

.btnSpan {
    display: block;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.12px;
    color: #8E8E93;
    cursor: pointer;

}

.addBtnDiv {
    height: 100%;
    position: relative;
    flex-basis: 60px;
}

.addBtnDivCer {
    width: 56px;
    height: 56px;
    /* background: red; */
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -16px;
    background: radial-gradient(98.92% 98.92% at 95.32% 45.48%, #F96060 0%, #F68888 100%), #F96060;

    font-size: 24px;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.24px;
    color: #FFFFFF;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;

    cursor: pointer;

}

.dNone {
    display: none;
}