.headerWarper {
    background: #F96060;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    color: white;
    height: 151px;
    position: relative;
}

.HeaderRow1 {
    width: 100%;
    text-align: center;
}

.headerTitle {
    font-size: 20px;
    line-height: 41px;
    text-align: center;
    color: #FFFFFF;
    padding: 0px;
    margin: 0px;


}

.OptionsDiv {
    width: 100%;
    height: 895px;
    position: absolute;
    top: 0;
    right: 0;
    background: #00000057;
    display: none;
    overflow: hidden;

}

.showOpt {
    display: initial;
}

.OptionsIcon {
    position: absolute;
    top: 40px;
    right: 10px;
    width: 20px;
    height: 21px;
    cursor: pointer;
}

.OptionsList {
    list-style: none;
    position: absolute;
    top: 50px;
    right: 10px;
    z-index: 1;
    color: black;
    padding: 0px;
    min-width: 228px;
    background: #FFFFFF;
    border-radius: 5px;
    box-shadow: 0px 0px 3px 0px #9E9E9E;
    transition: all 0.5s;
    transform: translateX(108%);

}

.showOptionsList {
    transform: translateX(0%);
}

.OptionsList li {
    width: 100%;
    font-size: 18px;
    line-height: 20px;
    color: #313131;
    margin: 10px auto;
    text-align: start;
    padding: 0px 10px;
    box-sizing: border-box;
    cursor: pointer;

}



.optionsSelectedIcon {
    float: right;
    margin-right: 5px;
}

.HeaderRow2 {
    width: 100%;
}

.tapsDiv {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.tabDiv {
    width: 70px;
    cursor: pointer;
}

.tabDiv.active {
    border-bottom: 3px solid white;
}

.tabDiv p {
    margin: 0px;
    margin-bottom: 10px;
    font-family: 'SFProText';
    font-size: 18px;
    line-height: 16px;
    text-align: center;
    letter-spacing: -0.28px;
    color: #FFFFFF;
}

.dNone {
    display: none;
}